.card {
    overflow: hidden;
    /* nasconde le parti che escono dell'immagine */
}

.card-img {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: transform 0.3s ease;
    display: block;
}

.card:hover .card-img {
    transform: scale(1.05);
    /* zoom interno */
}

.card-item>a {
  text-decoration: none;
}